import bus from '../assets/eventBus'
import { mapState } from 'vuex'
import store from '../store';
import { types } from './mutationTypes'
import mixinForClient from './mixin'
export default {
    name: 'QuickStartTutorial',
    mixins: [mixinForClient],
    data() {
        return {
            imgArray: [],
            carouselHeight: '',
            loadingStatus: 0,
        }
    },

    computed: {
        ...mapState([
            'keepAlive',
            'loginChange'
        ])
    },

    watch: {
        loadingStatus(newVal, oldVal) {
            if (newVal == 1) {
                store.dispatch('loading', { isChange: false });
            }
        },

        loginChange(newVal, oldVal) {
            if (this.$route.fullPath == '/quickStartTutorial' && !newVal) {
                this.loadingStatus = 0;
            }

            if (newVal != oldVal && !newVal && this.$route.fullPath.indexOf('quickStartTutorial') != -1) {

            }

            if (this.$route.fullPath == '/quickStartTutorial') {
                if (this.$store.state.loginChange) {
                    this.loadingStatus++;
                }
            }
        },
    },

    beforeRouteEnter(to, from, next) {
        next(vm => {
            if (vm.$store.state.keepAlive) {
                to.meta.keepAlive = true;
                store.commit(types.ALIVE, { isAlive: false });
                setTimeout(() => {
                    vm.loadImg();
                }, 100);

                next();
            } else {
                if (!vm.$route.meta.keepAlive) {
                    vm.initData();
                    vm.$nextTick(() => {
                        vm.loadImg();
                    })
                    next();
                }
            }
        })
    },

    beforeRouteLeave(to, from, next) {
        this.$route.meta.keepAlive = false;
        next();
    },

    mounted: function () {
        if (!this.$route.meta.keepAlive) {
            this.initData();
        }
        window.addEventListener('resize', () => {
            this.loadImg();
        }, false);
        this.loadImg();
    },

    created: function () {
        bus.$on('template', (msg) => {
            if (msg == 1) {
                this.initData();
            }
        })
    },

    methods: {
        initData() {
            this.imgArray.length = 0;
            for (let index = 0; index < 5; index++) {
                var obj = new Object();
                obj.src = "./Img/Intro/" + index + ".gif";
                this.imgArray.push(obj);
            }
        },

        loadImg() {
            this.carouselHeight = window.innerHeight - 16;
        },

        imgChange(cur, pre) {
            if (undefined != cur && cur >= 0 && cur < 5) {
                if (undefined != document.getElementsByClassName('carousel-img_')
                    && undefined != document.getElementsByClassName('carousel-img_')[cur].src) {
                    document.getElementsByClassName('carousel-img_')[cur].src = document.getElementsByClassName('carousel-img_')[cur].src;
                }
            }
        }
    }
}